export default {
    state: {
        dialog: null,
        project: {}
    },
    mutations: {
        UPDATE_DIALOG(state, payload) {
            state.dialog = payload
        }
    },
    actions: {
        switchDialog(context, payload) {
            let thisDialog = context.state.dialog

            thisDialog = payload
            context.commit('UPDATE_DIALOG', thisDialog)
        }
    }
}