import db from '../../firebase/firebaseinit';
import { convertDate } from '../../_helper/convertable';

export default {
    state: {
        invoiceData:            [],
        dialog:                 null,
        switchDialog:           null,
        invoiceLoaded:          null,
        currentInvoiceArray:    null,
        loadedInvoice:          null,
        editInvoice:            null,
    },
    mutations: {
        UPDATE_DIALOG(state) {
            state.dialog = !state.dialog
        },
        UPDATE_SWITCH_DIALOG(state){
            state.switchDialog = !state.switchDialog
        },
        SET_INVOICE_DATA(state, payload){
            state.invoiceData.push(payload);
            // console.log(state.invoiceData);
        },
        INVOICES_LOADED(state){
            state.invoiceLoaded = true;
            // console.log("invoiceLoaded status: " + state.invoiceLoaded)
        },
        SET_CURRENT_INVOICE(state, payload){
            console.log(state.invoiceData)
            let tempoArray = state.invoiceData.filter(invoice => {return invoice.invoiceId === payload})
            state.currentInvoiceArray = tempoArray;
            console.log(tempoArray)
        },
        SET_LOADED_INVOICE(state, payload){
            state.loadedInvoice = payload;
        },
        TOGGLE_EDIT_INVOICE(state){
            state.editInvoice = !state.editInvoice;
        }
    },
    actions: {
        switchDialog({commit}) {
            commit('UPDATE_DIALOG');
        },
        // Actions to treat data
        async GET_INVOICES({commit, state}) {
            const getData = db.collection('invoices');
            const results = await getData.get();
            results.forEach(doc => {
                if (!state.invoiceData.some( invoice => invoice.docId === doc.id)){
                    const data = {
                        docId:                  doc.id,
                        invoiceId:              doc.data().invoiceId,
                        billerStreetAddress:    doc.data().billerStreetAddress,
                        billerCity:             doc.data().billerCity,
                        billerZipCode:          doc.data().billerZipCode,
                        billerCountry:          doc.data().billerCountry,
                        clientName:             doc.data().clientName,
                        clientEmail:            doc.data().clientEmail,
                        clientStreetAddress:    doc.data().clientStreetAddress,
                        clientCity:             doc.data().clientCity,
                        clientZipCode:          doc.data().clientZipCode,
                        clientCountry:          doc.data().clientCountry,
                        invoiceDateUnix:        doc.data().invoiceDateUnix,
                        invoiceDate:            doc.data().invoiceDate,
                        paymentTerms:           doc.data().paymentTerms,
                        paymentDueDateUnix:     doc.data().paymentDueDateUnix,
                        paymentDueDate:         doc.data().paymentDueDate,
                        productDescription:     doc.data().productDescription,
                        invoiceItemList:        doc.data().invoiceItemList,
                        invoiceTotal:           doc.data().invoiceTotal,
                        invoicePending:         doc.data().invoicePending,
                        invoiceDraft:           doc.data().invoiceDraft,
                        invoicePaid:            doc.data().invoicePaid
                    };
                    commit('SET_INVOICE_DATA', data)
                }
            });
            commit('INVOICES_LOADED');
        },
        async getInvoiceInUse({commit}, payload){
            const getData = db.collection('invoices').where("invoiceId", "==", payload);
            const results = await getData.get()
            results.forEach((doc) => {
                const data = {
                    docId:                  doc.id,
                    invoiceId:              doc.data().invoiceId,
                    billerStreetAddress:    doc.data().billerStreetAddress,
                    billerCity:             doc.data().billerCity,
                    billerZipCode:          doc.data().billerZipCode,
                    billerCountry:          doc.data().billerCountry,
                    clientName:             doc.data().clientName,
                    clientEmail:            doc.data().clientEmail,
                    clientStreetAddress:    doc.data().clientStreetAddress,
                    clientCity:             doc.data().clientCity,
                    clientZipCode:          doc.data().clientZipCode,
                    clientCountry:          doc.data().clientCountry,
                    invoiceDateUnix:        doc.data().invoiceDateUnix,
                    invoiceDate:            convertDate(doc.data().invoiceDate),
                    paymentTerms:           doc.data().paymentTerms,
                    paymentDueDateUnix:     doc.data().paymentDueDateUnix,
                    paymentDueDate:         convertDate(doc.data().paymentDueDate),
                    productDescription:     doc.data().productDescription,
                    invoiceItemList:        doc.data().invoiceItemList,
                    invoiceTotal:           doc.data().invoiceTotal,
                    invoicePending:         doc.data().invoicePending,
                    invoiceDraft:           doc.data().invoiceDraft,
                    invoicePaid:            doc.data().invoicePaid,
                };
                commit('SET_LOADED_INVOICE',data);
            });
        }
    }
}