export default {
    state: {
        type:       null,
        message:    null
    },
    mutations: {
        ALERT_SUCCESS(state, message) {
            state.type      = 'alert-success';
            state.message   = message;
        },
        ALERT_ERROR(state, message) {
            state.type      = 'alert-error';
            state.message   = message;
        },
        ALERT_CLEAR(state) {
            state.type      = null;
            state.message   = null;
        }
    },
    actions: {
        success(context, payload) {
            context.commit('ALERT_SUCCESS', payload)
        },
        error(context, payload) {
            context.commit('ALERT_ERROR', payload)
        },
        clear({ commit }, payload ){
            commit('ALERT_SUCCESS', payload)
        }
    }
}