<template>
    <v-container class="container">
      <div class="mTransition">
        <transition name="toast">
          <Toast v-if="showToast"/>
        </transition>
        <v-text-field
          v-model="newTask"
          label="What are you working on?"
          solo
          @keypress.enter="addTask"
        >

        </v-text-field>
        <v-divider></v-divider>
        <div v-if="tasks.length">
          <transition-group
            tag="ul"
            name="listtood"
            appear
          >
            <li
              class="mt-3"
              v-for="task in tasks"
              :key="task.id"
              @click="deleteTask(task.id)"
            >
              <TDTCard :data="task" />
            </li>
          </transition-group>
        </div>
        <div v-else>
          <h3 class="subheading">Woohoo, nothing left todo !</h3>
        </div>
      </div>
    </v-container>
</template>

<script>
import TDTCard from '@/components/ToDo/tdTransitionCard'
import Toast from '@/components/Notifications/Alert'

export default {
    components: {
        TDTCard,
        Toast
    },
    data() {
        return{
            showToast:  false,
            tasks:      [],
            newTask:    null
        }
    },
    mounted() {
        this.getTasks();
    },
    watch: {
        tasks(newVal){
            this.tasks = newVal;
        }
    },
    methods: {
        // Tasks METHODS
        addTask() {
            if(this.newTask){
                const newElement = {
                'id': '111111111',
                'title': this.newTask
            }
            this.tasks.push(newElement);

            // Clear newTask value
            this.newTask = null;
            }else {
            this.showToast = true;
            setTimeout(() => this.showToast = false, 3000)
            // alert('Must filled title !')
            }

        },
        getTasks(){
            fetch('https://my.api.mockaroo.com/todo_transition.json?key=3082bdc0')
                .then(res => res.json())
                .then(data => {this.tasks = data})
                .catch(err => console.log({err}))
        },
        deleteTask(target) {
            const result = this.tasks.filter(tak => target.localeCompare(tak.id) != 0)
            this.tasks = result;
        }
    }
}
</script>

<style>
/* .listtood-enter-from {
  opacity: 0;
  transform: scale(0.6);
}
.listtood-enter-to {
  opacity: 1;
  transform: scale(1);
} */
.listtood-enter-active {
  /* transition: all 0.4s ease; */
  animation: wobble 0.5s ease;
}
.listtood-leave-from {
  opacity: 1;
  transform: scale(1);
}
.listtood-leave-to {
  opacity: 0;
  transform: scale(0.6);
}
.listtood-leave-active {
  transition: all 0.4s ease;
  position: absolute;
}
.listtood-move {
  transition: all 0.3s ease;
}
@keyframes wobble {
  0% { opacity: 0; transform: scale(0.6);}
  100% {opacity: 1; transform: scale(1);}
}
.toaster-enter-active{
    animation: wobble 0.5s ease;
}
.toaster-leave-active{
    animation: tscaleleave 0.5s ease;
}
@keyframes wobble {
    0%     {opacity: 0; transform: translateY(-60px);}
    50%   {opacity: 1; transform: translateY(0px);}
    60%   {transform: translateX(8px);}
    70%   {transform: translateX(-8px);}
    80%   {transform: translateX(4px);}
    90%   {transform: translateX(-4px);}
    100%   {transform: translateX(0);}
}
@keyframes tscaleleave {
    0%      {opacity: 1; transform: scale(1);}
    100%    {opacity: 0; transform: scale(0.6);}
}
</style>