<template>
    <v-container class="container">
        <div class="todo" v-if="airports">
            <div v-for="(airport, index) in airports" :key="index" @click="$store.dispatch('addToFavorites',airport)">
                <AirportCard :airport="airport"/>
            </div>
        </div>
        <h3 v-else>Woohoo , No (Airports) data is fetched !!!</h3>
        <h2 class="subheading" v-if="$store.state.airports.favorites.length">Favorites ({{ $store.state.airports.favorites.length }})</h2>
        <div class="todo">
            <div v-for="(airport, index) in $store.state.airports.favorites" :key="index" @click="$store.dispatch('removeFromFavorites',airport)">
                <AirportCard :airport="airport" />
            </div>
        </div>
    </v-container>
</template>

<script>
import AirportCard from '@/components/ToDo/AirportCard'

export default {
    components: {
        AirportCard
    },
    data() {
        return {
            airports:   null,
        }
    },
    mounted() {
        this.getAirports();
    },
    methods: {
        getAirports() {
            // this.$store.dispatch('getAirports')
            fetch('https://my.api.mockaroo.com/airports.json?key=3082bdc0')
                .then(res => res.json())
                .then(data => this.airports = data)
                .catch(err => console.log(err.message))
        },
    }
}
</script>

